.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center;
  color: #114E60;
}

.card {
  font-weight: 400;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}

.header {
  padding-top: 1rem;
  padding-left: 40px;
}

.wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  width: 200px;
  height: 45px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: #000;
  background-color: peachpuff;
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
}

.card-body {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  flex: 1 1 auto;
  min-height: 1px;
  padding-top: 1rem;
}

.footer {
  background-color: #929fba;
  padding-top: 1rem;
}

.modal-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  border-top: 1px solid #dee2e6;
  border-bottom-right-radius: calc(0.3rem - 1px);
  border-bottom-left-radius: calc(0.3rem - 1px);
  margin-bottom: 0.25rem;
  margin-right: 2.5rem;
  margin-left: 2.5rem;
  padding-top: 1rem
}

.img-fluid {
  background-repeat: no-repeat;
  background-image: url("./assets/images/background.png");
  background-size: cover;
}

.jumbotron {
  background-color: #fff;
  border-radius: .125rem;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.fab {
  color: black;
}

.fa-linkedin-square {
  color: #007bb6;
  background-image: linear-gradient( to bottom, transparent 20%, white 20%, white 93%, transparent 93% );
  background-size: 55%;
  background-position: 70% 0;
  background-repeat: no-repeat;
}

.fa-github {
  padding-left: 0.40em;
}

.fa-envelope {
  padding-left: 0.40em;
  color: #F5CEBE;
}

.row-input {
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.img-skills {
  max-width: 80%;
  height: auto;
}

.grid-container-skills {
  display: grid;
  align-items: left;
  justify-items: left;
  grid-template-columns: repeat(auto-fill, 170px);
  grid-template-rows: repeat(auto-fill, 100px);
}

.grid-item-skills {
  padding-left: 20px;
  text-align: left;
}

.portfolio-grid-item {
  text-align: left;
}

.grid-container-profile {
  display: grid;
  align-items: left;
  justify-items: left;
  grid-template-columns: repeat(2, 600px);
  grid-template-rows: repeat(3, 100px);
  margin: 1.5%;
}

.portfolio-grid-container {
  display: grid;
  grid-template-columns: 33% 33% 33%;

}



.App {
  font-family: 'Roboto', sans-serif;
}

.topnav {
  overflow: auto;
  padding: 0 20px;
  min-height: 9vh;
  background: #f4eee8;
  align-items: center;
  list-style: none;
}

.topnav a {
  float: left;
  color: #114E60;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-weight: bold;
}

.topnav-left {
  float: left;
  padding: 14px 16px;
  font-size: 25px;
}

.topnav-right {
  float: right;
  padding: 14px 16px;
  font-size: 17px;
  color: black;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
  list-style: none;
}

.navbar__link--active {
  background-color: #ddd;
  color: black;
  list-style: none;
}

p {
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.p-about {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  text-align: center;
}

.title {
  font-family: 'Roboto', sans-serif;
  font-size: 23px;
  font-weight: bold;
}

.title-job {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-weight: bold;
}

.title-company {
  font-family: 'Roboto', sans-serif;
  font-size: 17px;
}

.title-date {
  font-family: Roboto;
  font-size: 12px;
}

.title-uni {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
}

.title-concentration {
  font-family: Roboto;
  font-size: 15px;
}

li {
  font-family: Roboto;
}

.code {
  font-size: 20px;
  font-weight: bold;
  font-family: monospace;
  display: block;
  unicode-bidi: embed;
  white-space: pre-line;
  padding-bottom: 10px;
}


.avatar {
  padding-top: 10px;
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
  margin: auto;
  display: block;
  
}

.icon {
  padding: 10px;
}

.abstract-back {
  background-image: url('assets/images/background.png');
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}

.btn {
  background-color: #F4EEE8;
  border: 3px solid #F4EEE8;
}
.btn:hover, .btn:active {
  background-color: #F5CEBE;
  border: 3px solid #F5CEBE;
}


